import {TextInput} from "./text-input";
import {Validations} from "./../core/validations";
import {ATextInput} from "./abstract/a-text-input";
import {InputAddon} from "./input-addon";
import jQuery = require("jquery");

/**
 * @classdesc Input estilizado com ícone de relógio.
 * 
 * @example
 * 
 * import {TimeInput} from "mvcomponents/input";
 * 
 * let timeInput:TimeInput = new TimeInput();
 * 
 * this.append(timeInput);
 * 
 * @export
 * @extends {ATextInput}
 * @memberof module:mvcomponents/input
 */
export class TimeInput extends ATextInput {
	constructor(){
		super("text", "");

		this.addStyleName("InputTime")
			.append(new InputAddon()
				.setIcon("glyphicon glyphicon-time"));

		this.setSize(2);
		this.validator.add(Validations.maxLength(5));
	}
}
